import React from "react";

import './BannerReg.scss';
import '../../../../Shared/Scss/typography.scss';
import ManLaptop from "../../../../Shared/images/svg/ManLaptop/ManLaptop";
//import detect from "detect.js";

import '../../../../Shared/images/bgReg/retina/2k.jpg';
import '../../../../Shared/images/bgReg/retina/desktop.jpg';
import '../../../../Shared/images/bgReg/retina/tablet.jpg';
import '../../../../Shared/images/bgReg/retina/mobile.jpg';

import '../../../../Shared/images/bgReg/plain/2k.jpg';
import '../../../../Shared/images/bgReg/plain/desktop.jpg';
import '../../../../Shared/images/bgReg/plain/tablet.jpg';
import '../../../../Shared/images/bgReg/plain/mobile.jpg';

export default class BannerReg extends React.Component {
    state = {
        isTablet: false,
        isMobile: false,
    };
    contentText = this.props.contentText;
    maxMobileSize = 755;
    maxTabletSize = 960;

    deviceSizeChecker = () => {
        if (document.body.clientWidth > this.maxMobileSize && document.body.clientWidth < this.maxTabletSize) {
            this.setState({
                isTablet: true,
            });
        } else {
            this.setState({
                isTablet: false,
            });
        }

        if (document.body.clientWidth < this.maxMobileSize) {
            this.setState({
                isMobile: true,
            });
        } else {
            this.setState({
                isMobile: false,
            });
        }
    };

    componentDidMount() {
        this.deviceSizeChecker();
        window.addEventListener("resize", this.deviceSizeChecker);
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.contentText = nextProps.contentText;
    }

    render() {
        return (
            <section className="bannerReg">
                <div className="bannerReg__container">
                    <h2 className="heading2 bannerReg__title">{this.contentText.title}</h2>
                    <div className="bannerReg__content">
                        <p className="paragraph2 bannerReg__content-text">
                            <p>{this.contentText.text1}</p>
                            <br/>
                            <p>{this.contentText.text2}</p>
                            <br/>
                            <p>{this.contentText.text3}</p>
                        </p>
                        <div className="bannerReg__content-manLaptop">
                            <ManLaptop isTablet={this.state.isTablet} isMobile={this.state.isMobile}/>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}