import React from "react";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import CaretDown from "../../../../../../Shared/icons/CaretDown/CaretDown";
import '../../../../../Styleguide/Components/Fields/Fields.scss';

import './SelectInput.scss'

function SelectInput() {

    const [value, setValue] = React.useState('');
    const handleChange = event => {
        setValue(event.target.value);
    };

    const [labelWidth, setLabelWidth] = React.useState(0);
    const inputLabel = React.useRef(null);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    return (
        <div className="selectInput">
            <FormControl variant="outlined" className="fields__item-select selectInput-item">
                <InputLabel ref={inputLabel}>
                    Select your position
                </InputLabel>
                <Select
                    labelId="demo-simple-select-outlined-label"
                    id="demo-simple-select-outlined"
                    value={value}
                    onChange={handleChange}
                    labelWidth={labelWidth}
                    IconComponent={CaretDown}
                >
                    <MenuItem value={1}>First item</MenuItem>
                    <MenuItem value={2}>Second item</MenuItem>
                    <MenuItem value={3}>Third item</MenuItem>
                </Select>
            </FormControl>
        </div>
    )
}

export default SelectInput;