import React from "react";

import './CurrentUser.scss';
import avatarPlaceholder from "../../../../Shared/images/avatarPlaceholder.png";
export default class CurrentUser extends React.Component {
    constructor(props) {
        super(props);
        this.isMobile = this.props.isMobile;

        this.state = {
            userImg: avatarPlaceholder,
        }
    }

    componentDidMount() {
        const userImg = new Image();
        userImg.src = this.props.user.photo;

        userImg.onload = () => {
            this.setState({userImg: userImg.src});
            console.log(this.props.user);
        };
    }

    componentWillReceiveProps(nextProps, nextContext) {
        const userImg = new Image();
        userImg.src = nextProps.user.photo;

        userImg.onload = () => {
            this.setState({userImg: userImg.src});
            console.log(this.props.user);
        };
    }

    render() {
        const user = this.props.user;

        return (
            <div className={`current-user${this.isMobile ? ' mobile' : ''}`}>
                <div className={`current-user__info${this.isMobile ? ' mobile' : ''}`}>
                    <div className={`current-user__name${this.isMobile ? ' mobile' : ''}${user.name ? '' : ' hide'}`}>{user.name}</div>
                    <div className={`current-user__email${this.isMobile ? ' mobile' : ''}${user.email ? '' : ' hide'}`}>{user.email}</div>
                </div>
                <div className={`current-user__photo${this.isMobile ? ' mobile' : ''}`}>
                    <img src={this.state.userImg} alt="user"/>
                </div>
            </div>
        )
    }
}